<template>
  <v-app>
    <v-toolbar app>
      <v-img
        v-if="isHomeCare"
        :src="require('@/assets/chc-logo.png')"
        height="90"
        width="300"
        contain
      />
      <v-img
        v-if="isHospice"
        :src="require('@/assets/gsh-logo.png')"
        height="90"
        width="300"
        contain
      />

      <v-toolbar-title class="headline text-uppercase">
        <span class="font-weight-light">Physician Referral Form</span>
      </v-toolbar-title>
    </v-toolbar>

    <v-content class="pt-6">
      <v-container fluid>
        <v-row justify="center" align="start">
          <v-col cols="12" md="8">
            <!-- NOTICE -->
            <div v-if="redirecting" class="text-center">
              <p class="text-h4 font-weight-bold mb-3">
                The Physician Referral Form has been moved to a different area.
              </p>
              <p class="text-subtitle-1 mb-4">
                If you are not redirected, <a :href="targetURL">click here</a>.
              </p>
              <p class="text-body-1">
                Please update your bookmarks to the new address.
              </p>
            </div>

            <!-- ORIGINAL FORMS -->
            <HomeCareReferralForm v-else-if="isHomeCare" />
            <HospiceReferralForm  v-else-if="isHospice"  />
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>


<script>
import HomeCareReferralForm from "./components/HomeCareReferralForm";
import HospiceReferralForm  from "./components/HospiceReferralForm";

const NEW_BASE = "https://api002.chsli.org/apps/physician-referral-form/";

export default {
  name: "App",
  components: { HomeCareReferralForm, HospiceReferralForm },

  data() {
    return {
      form: null,
      redirecting: false,
      targetURL: ""
    };
  },

  created() {
    // grab the ?form= param (defaults to homecare)
    const params = new URLSearchParams(window.location.search);
    this.form = params.get("form") || "homecare";

    // build where we need to send them
    this.targetURL = `${NEW_BASE}?form=${encodeURIComponent(this.form)}`;

    // if were not already on the new-form page -> kick off redirect
    const here = window.location.origin + window.location.pathname;
    const dest = new URL(this.targetURL).origin + new URL(this.targetURL).pathname;
    if (here !== dest) {
      this.redirecting = true;

      // wait 3 seconds so user can read the notice, then redirect
      setTimeout(() => {
        window.location.replace(this.targetURL);
      }, 3000);
    }
  },

  computed: {
    isHomeCare() {
      return this.form === "homecare";
    },
    isHospice() {
      return this.form === "hospice";
    }
  }
};
</script>

<style>
.v-toolbar,
.v-toolbar__content {
  height: 100px !important;
}

.v-toolbar__content .v-image__image {
  width: 300px !important;
}

.v-content {
  padding-top: 100px !important;
}
</style>

