<script>
import axios from "axios";
import FileDragDrop from "./FileDragDrop";

//const url = "http://localhost:3001";
const url = "https://api002.chsli.org/homecare/";
//const url = "https://api-1.chsli.org:8000/homecare";

export default {
  components: {
    FileDragDrop
  },
  data: () => ({
    ref: {},
    refFiles: [], 
    menuDOB: false,
    menuOnsetDate: false,
    menuLastVisitDate: false,
    menuEncounterDate: false,
    //    formState: null,
    dialog: false,
    snackbar: {
      show: false,
      color: "",
      message: ""
    }
  }),
  mounted() {
    this.$refs.form.reset();
    this.ref = this.initializeRef();
  },
  watch: {
    menuDOB(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },
  methods: {
    saveDOB(date) {
      this.$refs.menuDOB.save(date);
      // clear errors if needed.  vee-validate has an issue with disabled fields
      const field = this.$validator.fields.find({ name: "Date of Birth" });
      field.reset();
      this.$validator.errors.remove(field.name, field.scope);
    },
    updateFileList(obj) {
      this.refFiles = obj;
    },
    cancelForm() {
      this.dialog = false;
      this.ref = this.initializeRef();
    },
    submitForm() {
      this.$validator.validate().then(async valid => {
        if (!valid) {
          this.$el
            .querySelector(
              '[name="' + this.$validator.errors.items[0].field + '"]'
            )
            .scrollIntoView();
          window.scrollBy(0, -120); // adjust scrolling higher
          return;
        }

        let formData = new FormData();

        for (let key in this.ref) {
          formData.append(key, this.ref[key]);
        }

        for (var i = 0; i < this.refFiles.length; i++) {
          formData.append("files[" + i + "]", this.refFiles[i]);
        }

        axios
          .post(url, formData)
          .then((result) => {
            this.snackbar.color = "green";
            this.snackbar.message =
              "Your referral has been successfully submitted.  Thank you.";
            this.snackbar.show = true;

            this.ref = this.initializeRef();
            this.$refs.fileDragDrop.reset(); // reset the child component
            this.$refs.form.reset();
          })
          .catch(error => {
            this.snackbar.color = "red";
            this.snackbar.message =
              "We're sorry, we're not able to send this information at the moment, please try again later.  " +
              error;
            this.snackbar.show = true;
          });
      });
    },
    initializeRef() {
      return {
        form: "hospice",
        ptFirstName: "",
        ptMiddleInitial: "",
        ptLastName: "",
        ptAddress: "",
        ptCity: "",
        ptZip: "",
        ptPhone: "",
        ptSSN: "",
        ptDOB: "",
        ptGender: "",
        ptInsurance1: "",
        ptPolicy1: "",
        ptInsurance2: "",
        ptPolicy2: "",
        ptDiagnosis1: "",
        ptDiagnosis2: "",
        ptMedicalHistory: "",
        ptReferralType: [],
        ptOnsetDate: "",
        ptRelationshipLengthYears: "",
        ptRelationshipLengthMonths: "",
        ptLastVisitDate: "",
        cgFirstName: "",
        cgLastName: "",
        cgPhone: "",
        cgSpecialInstructions: "",
        drFirstName: "",
        drLastName: "",
        drAddress: "",
        drCity: "",
        drZip: "",
        drPhone: "",
        drEmail: "",
        drLicense: "",
        drNPI: "",
        drReferralAgree: null,
        drCertifyIllness: null,
        drPatientAgree: null
      };
    },
    initializeRef_1() {
      return {
        form: "hospice",
        ptFirstName: "zzzz",
        ptMiddleInitial: "zzzz",
        ptLastName: "zzzz",
        ptAddress: "zzzz",
        ptCity: "zzzz",
        ptZip: "33333",
        ptPhone: "",
        ptDOB: "06/13/1990",
        ptGender: "",
        ptInsurance1: "zzzz",
        ptPolicy1: "zzzz",
        ptInsurance2: "",
        ptPolicy2: "",
        ptDiagnosis1: "",
        ptDiagnosis2: "",
        ptMedicalHistory: "",
        ptReferralType: [],
        ptOnsetDate: "",
        ptRelationshipLengthYears: "",
        ptRelationshipLengthMonths: "",
        ptLastVisitDate: "",
        drEncounterDate: "",
        drMedicalFindings: "",
        drFirstName: "zzzz",
        drLastName: "zzzz",
        drAddress: "zzzz",
        drCity: "zzzz",
        drZip: "12311",
        drPhone: "",
        drEmail: "",
        drLicense: "",
        drNPI: "",
        drReferralAgree: "yes",
        drCertifyIllness: "yes",
        drPatientAgree: "yes"
      };
    }
  }
};
</script>

